<template>
  <div class="flex justify-between">
    <!-- 좌측 정렬-->
    <div class="flex">
      <router-link class="px-3" to="/">
        <img
          class="w-8 h-8 rounded-full"
          alt="logo"
          src="@/assets/logo-sm.png"
        />
      </router-link>

      <div class="px-3">
        <router-link to="/about">소개</router-link>
      </div>
      <div class="px-3">
        <router-link to="/welcome">환영</router-link>
      </div>
    </div>
    <!-- 우측 정렬-->
    <div class="flex" v-if="!waitResponse">
      <div class="mr-3">
        <router-link
          v-if="!$store.getters.isLogin"
          to="/login"
          class="
            py-1
            px-3
            font-bold
            rounded
            bg-indigo-600
            text-white
            hover:bg-indigo-300
            hover:text-black
            transition-colors
            duration-300
          "
          >로그인</router-link
        >
        <router-link
          v-else
          to="/profile"
          class="
            py-1
            px-3
            font-bold
            rounded
            bg-indigo-600
            text-white
            transition-colors
            duration-300
          "
          >{{ $store.getters.userName }}</router-link
        >
      </div>
      <div>
        <router-link
          v-if="!$store.getters.isLogin"
          to="/signup"
          class="
            py-1
            px-3
            font-bold
            rounded
            bg-yellow-300
            text-yellow-900
            hover:bg-yellow-100
            hover:text-yellow-600
            transition-colors
            duration-300
          "
          >가입</router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      waitResponse: false,
    };
  },
  async mounted() {
    const loginToken = localStorage.getItem("loginToken");
    if (loginToken === null) {
      return;
    }

    const reqData = {
      api: "loginWithToken",
      loginToken: loginToken,
    };
    console.log("token exists", reqData);

    this.waitResponse = true;
    const res = await this.$store.callAsync(reqData);
    this.waitResponse = false;

    if (res.code != 100) {
      console.log("loginWithToken ng", res.message);
      if (res.code == -2) {
        localStorage.setItem("loginToken", null);
        return;
      }
      return;
    }

    console.log("loginWithToken ok", res);
    this.$store.commit("setUser", res.user);
  },
};
</script>
