<template>
  <div class="home">
    <div v-if="!$store.getters.isLogin">
      <div>타일을 만들 수 있고</div>
      <div>(제작중) 내 그림에 타일을 불일 수 있습니다.</div>
      <div>(제작중) 내 그림에 다른 유저의 타일도 붙일 수 있습니다.</div>
      <div>
        현재는 guest 계정을 이용중입니다. <br />
        데이터가 삭제되거나 변경될 수 있으니 저장이 필요하시면 가입을 해주세요
      </div>
    </div>

    <div v-if="loadingSuccess" class="py-2">
      <div
        v-for="data in $store.state.listPicture"
        :key="data.uid"
        class="flex py-1"
      >
        <div class="px-2 py-1">{{ data.uid }}</div>
        <div class="px-2 py-1">{{ data.name }}</div>
        <button
          class="px-2 py-1 bg-indigo-600 text-white"
          @click="onEdit(data.uid)"
        >
          수정
        </button>
        <button
          class="px-2 py-1 bg-red-600 text-white"
          @click="onDelete(data.uid)"
        >
          X
        </button>
      </div>
      <div class="flex py-2">
        <input
          v-model="inputPictureName"
          class="px-2 py-1 border-2 border-black"
        />
        <button class="px-2 py-1 bg-indigo-600 text-white" @click="onAdd()">
          추가
        </button>
      </div>
    </div>
    <div class="text-red-600">
      {{ errorMessage }}
    </div>

    <!-- <iframe
      src="/game1/game.html"
      width="640"
      height="480"
      scrolling="no"
      frameborder="0"
    >
    </iframe> -->
    <!-- <comp-1 /> -->

    <!-- <a href="/game2/index.html" target="_blank">DOT IMAGE</a> -->
    <!-- <button class="bg-indigo-300 m-2 p-2 rounded" @click="onOpen()">
      OPEN
    </button> -->
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      loading: false,
      errorMessage: "",
      loadingSuccess: false,
      inputPictureName: "",
      loadingAddName: false,
      loadingDelete: false,
    };
  },
  async mounted() {
    console.log("home mounted");
    if (this.$store.state.listPicture === null) {
      // 리스트가 없으면 불러온다.
      const reqData = {
        api: "getPictureList",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
      };

      console.log("getPictureList req", reqData);

      this.loading = true;
      const res = await this.$store.callAsync(reqData);
      this.loading = false;

      console.log("getPictureList res", res);

      if (res.code != 100) {
        this.errorMessage = res.message;
        return;
      }

      this.$store.commit("setListPicture", res.listPicture);
      this.loadingSuccess = true;
    } else {
      this.loadingSuccess = true;
    }
  },
  methods: {
    async onAdd() {
      if (this.loadingAddName) {
        return;
      }

      const reqData = {
        api: "addPicture",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
        inputName: this.inputPictureName,
      };

      this.loadingAddName = true;
      const res = await this.$store.callAsync(reqData);
      this.loadingAddName = false;

      if (res.code != 100) {
        this.errorMessage = res.message;
        return;
      }

      this.$store.commit("addPicture", res.newPicture);
      this.inputPictureName = "";
    },
    async onEdit(picUid) {
      console.log("onEdit", picUid);

      let pic = null;
      for (let i = 0; i < this.$store.state.listPicture.length; i++) {
        const data = this.$store.state.listPicture[i];
        if (data.uid !== picUid) {
          continue;
        }
        pic = data;
      }

      if (pic === null) {
        console.log("not found pic", picUid);
        return;
      }

      localStorage.setItem("curEditPic", JSON.stringify(pic));
      window.open("/game2/index.html", "_blank");
    },
    async onDelete(picUid) {
      console.log("onDelete", picUid);

      const reqData = {
        api: "delPicture",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
        picUid: picUid,
      };

      console.log("delPicture req", reqData);

      this.loadingAddName = true;
      const res = await this.$store.callAsync(reqData);
      this.loadingAddName = false;

      console.log("delPicture res", res);

      if (res.code != 100) {
        this.errorMessage = res.message;
        return;
      }

      this.$store.commit("delPicture", picUid);
      this.inputPictureName = "";
    },
  },
};
</script>
