import { createStore } from "vuex";
import ax from "axios";

let apiUrl = "https://api.metabusgo.xyz/api";
let uploadUrl = "https://api.metabusgo.xyz/upload";
if (process.env.NODE_ENV != "production") {
  apiUrl = "http://localhost/api";
  uploadUrl = "http://localhost/upload";
}

// 사용해야하는 url 이 많아져서 baseURL 이용하면 안되겠음
// axios.defaults.baseURL;

// csrf 문제 해결할려고 넣었는데 안되는거 같음
// axios.defaults.xsrfCookieName = "csrftoken";
// axios.defaults.xsrfHeaderName = "X-CSRFToken";
// axios.defaults.headers.common["X-CSRF-TOKEN"] = "hellow-localhost";

function call(reqData, resolve) {
  const axios = ax.create();
  axios
    .post(apiUrl, reqData)
    .then((res) => {
      if (res.data.code === undefined) {
        resolve({
          code: -1,
          message: "no data",
        });
        return;
      }

      resolve(res.data);
    })
    .catch((err) => {
      resolve({
        code: -1,
        message: `catch:${err.message}`,
      });
    });
}

async function callAsync(reqData) {
  // console.log(api_call, reqData);
  return new Promise((resolve) => {
    call(reqData, resolve);
  });
}

async function wait(sec) {
  return new Promise((resolve) => {
    const ms = Math.ceil(sec * 1000);
    setTimeout(function () {
      resolve();
    }, ms);
  });
}

let store = createStore({
  state: {
    user: {
      uid: 0,
      id: "",
      name: "",
    },
    userName: "guest",
    listPicture: null,
  },
  actions: {},
  modules: {},
  getters: {
    isLogin: function (state) {
      return state.user.uid != 0;
    },
    userName: function (state) {
      return state.userName;
    },
    userUid: function (state) {
      return state.user.uid;
    },
  },
  mutations: {
    setUser: (state, payload) => {
      state.user.uid = payload.uid;
      state.user.id = payload.id;
      state.user.name = payload.name;
      state.userName = state.user.id;
      state.listPicture = null;
    },
    clear: function (state) {
      state.user.uid = 0;
      state.user.id = "";
      state.user.name = "";
      state.userName = "";
    },
    setListPicture: function (state, payload) {
      state.listPicture = payload;
    },
    addPicture: function (state, payload) {
      state.listPicture.push(payload);
    },
    delPicture: function (state, payload) {
      let picUid = payload;
      for (let i = 0; i < state.listPicture.length; i++) {
        if (state.listPicture[i].uid !== picUid) {
          continue;
        }
        state.listPicture.splice(i, 1);
        break;
      }
    },
  },
});

store.callAsync = callAsync;
store.call = call;
store.wait = wait;
store.getLoginToken = () => {
  const loginToken = localStorage.getItem("loginToken");
  return loginToken;
};

store.getApiUrl = () => {
  //  axios.defaults.baseURL + "/api";
  return apiUrl;
};

store.uploadAsync = async (/* FormData */ fd) => {
  return new Promise((resolve) => {
    const axios = ax.create();
    axios
      .post(uploadUrl, fd)
      .then((res) => {
        // console.log("uploadAsync ok", res);
        resolve(res.data);
      })
      .catch((error) => {
        // console.error("uploadAsync ng", error.message);
        resolve({ code: -1, message: error.message });
      });
  });
};

store.getGUID = () => {
  return Math.random().toString(36).substr(2, 9);
};

export default store;
